.mask {
  position: absolute;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000;
  opacity: 0.6;
  filter: alpha(opacity=60);
  -moz-opacity: 0.6;
}

.alert {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1001;
  margin-top: -100px;
  margin-left: -120px;
  width: 240px;
  max-height: 240px;
  background: #FFFFFF;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.33);
  border-radius: 10px;
}

.alert .content {
  padding: 25px 0 66px;
}

.alert .foot {
  position: absolute;
  bottom: 20px;
  margin-top: 20px;
  width: 100%;
  text-align: center;
}

.alert .foot .button {
  width: 90px;
  height: 26px;
  display: inline-block;
  *display: inline;
  *zoom: 1;
  line-height: 26px;
  font-size: 12px;
  color: #fff;
  text-align: center;
  background: #038FFF;
  background-image: linear-gradient(-180deg, #0DB4FF 0%, #038FFF 100%);
  border: 1px solid #B1BECA;
  box-shadow: inset 0px -2px 6px 0px rgba(255, 255, 255, 0.6);
  border-radius: 4px;
  transform: scale(1);
}

.alert .foot .button:hover {
  transform: scale(1.1);
}

.alert .foot .button.single {
  color: #FFF703;
  margin-right: 10px;
}



