.navBar {
  width: 100%;
  margin-top: 20px;
}

.navBar li {
  height: 50px;
  line-height: 50px;
  margin-left: 4px;
}

.navBar li .tap {
  vertical-align: middle;
  margin-right: 15px;
  width: 24px;
  height: 24px;
  position: relative;
  top: -2px;
}

.navBar .nav.active {
  color: #033668;
  background: #F9FAFB;
  font-weight: bold;
  border: 0;
  border-radius: 4px 0 0 4px;
  box-shadow: none;
  position: relative;
  z-index: 10;
}

.navBar .nav {
  padding-left: 13px;
  height: 52px;
  display: block;
  color: #0066AC;
  border-top: solid 1px #cdcdcd \9;
  border-radius: 3px 0 0 3px;
  box-shadow: inset 0px 1px 0px 0px #CDCDCD,inset 0px 2px 0px 0px #FFF;
}

.navBar .last {
  border-bottom: solid 1px #cdcdcd \9;
  box-shadow: inset 0px 1px 0px 0px #CDCDCD,inset 0px 2px 0px 0px #FFF,inset 0px -1px 0px 0px #fff,inset 0px -2px 0px 0px #cdcdcd;
}

@media screen and (min-width: 0\0) {
  .navBar .nav {
    border-top: 0;
  }
  .navBar .last {
    border-bottom: 0;
  }
}

.navBar .tap0 {
  background: url(../../assets/ic_tap1.png) no-repeat;
}

.navBar .tap1 {
  background: url(../../assets/ic_tap2.png) no-repeat;
}

.navBar .tap2 {
  background: url(../../assets/ic_tap3.png) no-repeat;
}

.navBar .nav.active .tap0 {
  background: url(../../assets/ic_tap1_a.png) no-repeat;
}

.navBar .nav.active .tap1 {
  background: url(../../assets/ic_tap2_a.png) no-repeat;
}

.navBar .nav.active .tap2 {
  background: url(../../assets/ic_tap3_a.png) no-repeat;
}



